import axios from 'axios'
const baseUrl = '/api/blogs'

let token = null

const setToken = (newToken) => {
    token = `Bearer ${newToken}`
}

const getAll = async () => {
  const response = await axios.get(baseUrl)
  return response.data
}

const create = async newObj => {
  const config = {
    headers: { Authorization: token }
  }
  const response = await axios.post(baseUrl, newObj, config )
  return response.data
}

const update = async updateObj => {
  const response = await axios.put(`${baseUrl}/${updateObj.id}`, updateObj)
  return response.data
}

const deleteItem = async deleteObj => {
  const config = {
    headers: { Authorization: token }
  }
  console.log('config header', config)
  console.log('deleteObj received', deleteObj)
  console.log('path using in axios delete', `${baseUrl}/${deleteObj.id}`)
  const response = await axios.delete(`${baseUrl}/${deleteObj.id}`, config, deleteObj)
  console.log('response from the server when deleting', response)
  return response.data
}

export default { getAll, setToken, create, update, deleteItem }